import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class MusicService {
	private songNumber: number = 6;

	private volume: BehaviorSubject<number> = new BehaviorSubject(0);
	private song: BehaviorSubject<number> = new BehaviorSubject(this.random());

	private random(): number {
		return Math.round(Math.random() * (this.songNumber - 1) + 1);
	}

	public songShuffle(): void {
		this.song.next(this.random());
	}

	public getSong(): BehaviorSubject<number> {
		return this.song;
	}

	public getVolume(): BehaviorSubject<number> {
		return this.volume;
	}

	public setVolume(volume: number): void {
		this.volume.next(volume);
	}
}
