import {
	Component,
	OnInit,
	OnDestroy,
	ViewChild,
	ElementRef,
	AfterViewInit,
} from '@angular/core';
import { Subscription } from 'rxjs';

import { GeoService } from '../../services/geo.service';

import { AirFacade } from 'src/app/store/air';
import { WeatherFacade } from '../../store/weather/weather.facade';

import { WeatherData } from '../../models/weather-data.model';

@Component({
	selector: 'app-info-page',
	templateUrl: './info-page.component.html',
	styleUrls: ['./info-page.component.scss'],
})
export class InfoPageComponent implements OnInit, AfterViewInit, OnDestroy {
	private subGetLocationByCoordinatesFromQueryParams: Subscription;
	private subSelectWeatherIcon: Subscription;

	@ViewChild('icon') private icon: ElementRef;

	public weatherData: WeatherData;
	public location: string = '';

	constructor(
		private geoService: GeoService,
		private airFacade: AirFacade,
		private weatherFacade: WeatherFacade
	) {}

	public ngOnInit(): void {
		this.airFacade.getAirDataByApi();
		this.weatherFacade.getWeatherDataByApi();

		this.subGetLocationByCoordinatesFromQueryParams = this.geoService
			.getLocationByCoordinatesFromQueryParams()
			.subscribe((location: string) => {
				this.location = location;
			});
	}

	public ngAfterViewInit(): void {
		this.subSelectWeatherIcon = this.weatherFacade
			.selectWeatherIcon()
			.subscribe((icon: string) => {
				if (icon !== null || icon !== undefined) {
					this.icon.nativeElement.innerHTML = `<img src="https://www.weatherbit.io/static/img/icons/${icon}.png" />`;
				}
			});
	}

	public ngOnDestroy(): void {
		this.subGetLocationByCoordinatesFromQueryParams.unsubscribe();
		this.subSelectWeatherIcon.unsubscribe();
	}
}
