import { Component, ViewChild, ElementRef } from '@angular/core';
import { AirFacade } from 'src/app/store/air';
import { AirData } from 'src/app/models/air-data.model';
import { Subscription } from 'rxjs';
import { OnInit, OnDestroy } from '@angular/core';
import { UserFacade } from '../../../store/user/user.facade';

@Component({
	selector: 'app-aqi',
	templateUrl: './aqi.component.html',
	styleUrls: ['./aqi.component.scss'],
})
export class AqiComponent implements OnInit, OnDestroy {
	@ViewChild('progressBar') private progressBar: ElementRef;

	private subSelectAirData: Subscription;
	private subSelectCurrentTheme: Subscription;
	private subSelectCurrentLanguage: Subscription;

	public aqi: number;
	public aqiString: string;

	public isTooltipVisiable: boolean = false;

	constructor(private airFacade: AirFacade, private userFacade: UserFacade) {}

	public outerStrokeColor(): string {
		if (this.aqi !== null) {
			if (this.aqi > 0 && this.aqi <= 50) {
				return '#0c0';
			} else if (this.aqi > 50 && this.aqi <= 100) {
				return '#6c0';
			} else if (this.aqi > 100 && this.aqi <= 200) {
				return '#ff0';
			} else if (this.aqi > 200 && this.aqi <= 300) {
				return '#f90';
			} else if (this.aqi > 300 && this.aqi <= 400) {
				return 'red';
			} else if (this.aqi > 400 && this.aqi <= 500) {
				return 'brown';
			}
		}
	}

	public ngOnInit(): void {
		this.subSelectAirData = this.airFacade
			.selectAirData()
			.subscribe((airData: AirData) => {
				this.aqi = airData.aqi;
				if (this.progressBar) {
					this.progressBar.nativeElement.classList =
						'aqi--progress-bar aqi--progress-bar__active';
				}
			});

		this.subSelectCurrentLanguage = this.userFacade
			.selectCurrentLanguage()
			.subscribe((lang: string) => {
				if (lang === 'en') {
					this.aqiString = 'AQI';
				} else if (lang === 'ru') {
					this.aqiString = '–ò–ö–í';
				}
			});
	}

	public ngOnDestroy(): void {
		this.subSelectAirData.unsubscribe();
		this.subSelectCurrentLanguage.unsubscribe();
		this.subSelectCurrentTheme.unsubscribe();

		this.progressBar.nativeElement.classList = 'aqi--progress-bar';
	}

	public tooltipToggle(): void {
		this.isTooltipVisiable = !this.isTooltipVisiable;
	}

	public titleColor(): string {
		let color: string;
		this.subSelectCurrentTheme = this.userFacade
			.selectCurrentTheme()
			.subscribe((theme: string) => {
				if (theme === 'light') {
					color = '#0f0f0f';
				} else if (theme === 'dark') {
					color = '#fffefe';
				}
			});
		return color;
	}
}
