import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';

import { Howl } from 'howler';
import { Subscription } from 'rxjs';
import { MusicService } from 'src/app/components/music/music.service';

@Component({
	selector: 'app-music',
	template: '',
})
export class MusicComponent implements OnInit, AfterViewInit, OnDestroy {
	private subGetVolume: Subscription;
	private subGetSong: Subscription;

	private sound: Howl;
	private volume: number = 0;

	constructor(private musicService: MusicService) {}

	public ngOnInit(): void {
		this.sound = new Howl({
			src: [`../assets/music/1.mp3`],
			autoplay: false,
			loop: true,
			volume: this.volume,
		});

		this.subGetSong = this.musicService
			.getSong()
			.subscribe((song: number) => {
				this.sound.stop();
				this.sound = new Howl({
					src: [`../assets/music/${song}.mp3`],
					autoplay: false,
					loop: true,
					volume: this.volume,
				});
				this.sound.play();
			});

		this.subGetVolume = this.musicService
			.getVolume()
			.subscribe((volume: number) => {
				this.sound.volume(volume);
				this.volume = volume;

				volume === 0 ? this.sound.mute(true) : this.sound.mute(false);
			});
	}

	public ngAfterViewInit() {
		this.sound.play();
	}

	public ngOnDestroy(): void {
		this.subGetVolume.unsubscribe();
		this.subGetSong.unsubscribe();
	}
}
