import {
	createFeatureSelector,
	createSelector,
	MemoizedSelector,
} from '@ngrx/store';

import { IAppState, FeatureKey } from '../index';
import { IUserState } from './user.state';

export const selectLanguageState: MemoizedSelector<
	IAppState,
	IUserState
> = createFeatureSelector<IAppState, IUserState>(FeatureKey.User);

export const selectCurrentLanguage: MemoizedSelector<
	IAppState,
	string
> = createSelector(selectLanguageState, (state: IUserState) => state.language);

export const selectCurrentTheme: MemoizedSelector<
	IAppState,
	string
> = createSelector(selectLanguageState, (state: IUserState) => state.theme);
